.act-banner{
  height: 370px;
  background-image: url("../images/Rectangle 93.png");
  .act-banner-text {
    color:white;
    font-size: 34px;
    font-weight: 900;
  }
}
.city-group {
  height: 55px;
  position: relative;
  .label-icon{
    position: absolute;
    height: 100%;
    width: 50px;
    text-align: center;
    line-height: 55px;
    padding-left: 5px;
  }
  .form-select{
    padding-left: 55px;
    border-radius: 55px;
    font-size: 18px;
    line-height: 43px;

  }
}
.nav-group{
  border-bottom: 1px solid #BDBDBD;
  margin-bottom: 55px;
  .nav-link.active{
    border-bottom: 9px solid  #5B9BD5;
    margin-bottom: 0;
  }
  .form-select{
    width: 249px;
    padding-left: 55px;
    border-radius: 55px;
    font-size: 24px;
  }
}