body {
  background-color: #E5E5E5;
}
.banner {
  height: 450px;
  background-image: url("../images/Rectangle45.png");
  .input-search{
    width: 40%;
    height: 55px;
    position: relative;
  }
  input[type="text"]{
    padding-left: 55px;
    border-radius: 55px;
    font-size: 24px;
  }
  input[type="text"]::placeholder {
    color:#BDBDBD;
  }
}
.featured {
  padding-top: 71px;
  margin-bottom: 65px;
  h4 {
    margin-bottom: 65px;
  }
  .card{
    height: 100%;
  }
}
.populardestination{
  margin-bottom: 90px;
}
.recommended{
  margin-bottom: 144px;
}
.swiper {
  width: 100%;
  height: auto;
  --swiper-navigation-color: #fff;/*  */
  --swiper-navigation-background:#000;
  --swiper-navigation-size: 20px;/*  */
  .swiper-button-next,.swiper-button-prev{
    background-color: #D69F8F;
    width: 40px;
    height: 40px;
    border-radius: 40px;
  }
}

.card{
  height: 100%;
}